'use client';

import { useState, useRef, useEffect } from 'react';
import { saveSafetyEvent, generateEventId, getCriticalEvents } from '@/lib/safety-storage';
import type { SafetyAnalysis } from '@/lib/site-guardian';
import type { SafetyEvent } from '@/lib/safety-storage';
import LandingPage from './components/LandingPage';
import HourglassProgress from './components/HourglassProgress';

export default function SiteGuardianDashboard() {
  const [showLanding, setShowLanding] = useState(true);
  const [file, setFile] = useState<File | null>(null);
  const [analyzing, setAnalyzing] = useState(false);
  const [analysisProgress, setAnalysisProgress] = useState(0);
  const [currentAnalysis, setCurrentAnalysis] = useState<SafetyAnalysis | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [audioEnabled, setAudioEnabled] = useState(true);
  const [criticalAlerts, setCriticalAlerts] = useState<SafetyEvent[]>([]);
  const [annotatedImage, setAnnotatedImage] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const videoRef = useRef<HTMLVideoElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    setCriticalAlerts(getCriticalEvents());
  }, []);

  // Annotate faces on image for workers without hard hats
  const annotateFaces = (imageSrc: string, analysis: SafetyAnalysis): Promise<string> => {
    return new Promise((resolve, reject) => {
      const img = new Image();
      img.onload = () => {
        const canvas = canvasRef.current || document.createElement('canvas');
        canvas.width = img.width;
        canvas.height = img.height;
        const ctx = canvas.getContext('2d');
        
        if (!ctx) {
          reject(new Error('Could not get canvas context'));
          return;
        }

        // Draw original image
        ctx.drawImage(img, 0, 0);

        // Draw squares on faces of workers without hard hats or with non-compliant head coverings
        analysis.detected.forEach((worker) => {
          const needsPrivacyBlur = !worker.ppe.hardHat && 
            (worker.ppe.headCovering === 'BARE_HEAD' || 
             worker.ppe.headCovering === 'TURBAN' || 
             worker.ppe.headCovering === 'MUSLIM_TAQIYAH' ||
             worker.ppe.headCovering === 'UNKNOWN');
          
          if (needsPrivacyBlur && worker.faceCoordinates) {
            const { x, y, width, height } = worker.faceCoordinates;
            const boxX = (x / 100) * canvas.width;
            const boxY = (y / 100) * canvas.height;
            const boxWidth = (width / 100) * canvas.width;
            const boxHeight = (height / 100) * canvas.height;

            // Draw semi-transparent purple box
            ctx.fillStyle = 'rgba(168, 85, 247, 0.4)'; // purple-500 with opacity
            ctx.fillRect(boxX, boxY, boxWidth, boxHeight);
            
            // Draw border
            ctx.strokeStyle = '#a855f7'; // purple-500
            ctx.lineWidth = 3;
            ctx.strokeRect(boxX, boxY, boxWidth, boxHeight);

            // Draw label with head covering type
            ctx.fillStyle = '#7c3aed'; // purple-600
            ctx.font = 'bold 14px system-ui';
            let label = '‚ö†Ô∏è NO HARD HAT';
            if (worker.ppe.headCovering === 'TURBAN') {
              label = '‚ö†Ô∏è TURBAN (No Hard Hat)';
            } else if (worker.ppe.headCovering === 'MUSLIM_TAQIYAH') {
              label = '‚ö†Ô∏è TAQIYAH (No Hard Hat)';
            } else if (worker.ppe.headCovering === 'BARE_HEAD') {
              label = '‚ö†Ô∏è BARE HEAD';
            }
            ctx.fillText(label, boxX, boxY - 5);
          }
        });

        resolve(canvas.toDataURL('image/jpeg', 0.9));
      };
      img.onerror = reject;
      img.src = imageSrc;
    });
  };

  const extractFrameFromVideo = (videoFile: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const video = document.createElement('video');
      video.preload = 'metadata';
      video.muted = true;
      video.playsInline = true;
      
      video.onloadedmetadata = () => {
        video.currentTime = Math.min(video.duration / 2, 1);
      };
      
      video.onseeked = () => {
        try {
          const canvas = document.createElement('canvas');
          canvas.width = video.videoWidth;
          canvas.height = video.videoHeight;
          const ctx = canvas.getContext('2d');
          
          if (!ctx) {
            reject(new Error('Could not get canvas context'));
            return;
          }
          
          ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
          const frameDataUrl = canvas.toDataURL('image/jpeg', 0.8);
          resolve(frameDataUrl);
        } catch (err) {
          reject(err);
        }
      };
      
      video.onerror = () => {
        reject(new Error('Failed to load video'));
      };
      
      video.src = URL.createObjectURL(videoFile);
    });
  };

  const speakIntervention = (text: string, level?: string) => {
    if (!audioEnabled) return;
    
    if ('speechSynthesis' in window) {
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.rate = 0.9;
      utterance.volume = level === 'EMERGENCY_ALARM' || level === 'AUDIO_LEVEL_3' ? 1.0 : 
                         level === 'AUDIO_LEVEL_2' ? 0.8 : 0.6;
      utterance.pitch = 1.0;
      window.speechSynthesis.speak(utterance);
    }
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0];
    if (selectedFile && selectedFile.type.startsWith('video/')) {
      setFile(selectedFile);
      setError(null);
      setCurrentAnalysis(null);
      setAnnotatedImage(null);
    } else {
      setError('Please select a valid video file');
    }
  };

  const handleAnalyze = async () => {
    if (!file) {
      setError('Please select a video file first');
      return;
    }

    setAnalyzing(true);
    setAnalysisProgress(0);
    setError(null);
    setCurrentAnalysis(null);
    setAnnotatedImage(null);

    // Simulate progress
    const progressInterval = setInterval(() => {
      setAnalysisProgress((prev) => {
        if (prev >= 90) {
          return 90;
        }
        return prev + 10;
      });
    }, 200);

    // Simulate progress
    const progressInterval = setInterval(() => {
      setAnalysisProgress((prev) => {
        if (prev >= 90) {
          clearInterval(progressInterval);
          return 90;
        }
        return prev + 10;
      });
    }, 200);

    try {
      let frameImage: string | null = null;
      try {
        frameImage = await extractFrameFromVideo(file);
      } catch (frameError: any) {
        console.warn('Could not extract frame:', frameError);
      }

      const formData = new FormData();
      formData.append('video', file);
      if (frameImage) {
        formData.append('frame', frameImage);
      }

      const response = await fetch('/api/analyze-safety', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        clearInterval(progressInterval);
        let errorMessage = 'Failed to analyze safety frame';
        try {
          const errorData = await response.json();
          errorMessage = errorData.error || errorData.details || errorMessage;
        } catch (e) {
          errorMessage = `HTTP ${response.status}: ${response.statusText}`;
        }
        throw new Error(errorMessage);
      }

      clearInterval(progressInterval);
      setAnalysisProgress(95);

      const result: SafetyAnalysis = await response.json();
      setCurrentAnalysis(result);
      setAnalysisProgress(100);
      
      // Wait a moment before hiding progress
      setTimeout(() => {
        setAnalysisProgress(0);
      }, 500);

      // Annotate faces if we have a frame image
      if (frameImage && result.detected.some(w => !w.ppe.hardHat && w.faceCoordinates)) {
        try {
          const annotated = await annotateFaces(frameImage, result);
          setAnnotatedImage(annotated);
        } catch (annotError) {
          console.warn('Failed to annotate faces:', annotError);
          setAnnotatedImage(frameImage);
        }
      } else if (frameImage) {
        setAnnotatedImage(frameImage);
      }

      // Save event
      const videoUrl = URL.createObjectURL(file);
      const event: SafetyEvent = {
        id: generateEventId(),
        timestamp: result.timestamp,
        status: result.status,
        workers: result.detected,
        analysis: Array.isArray(result.analysis) ? result.analysis.join(' ') : result.analysis,
        action: result.action,
        voiceOutput: result.voiceOutput,
        audioLevel: result.audioLevel,
        resolved: false,
        videoUrl,
      };
      saveSafetyEvent(event);

      // Trigger audio intervention
      if (result.status !== 'üü¢ CLEAR' && result.voiceOutput) {
        speakIntervention(result.voiceOutput, result.audioLevel);
      }

      if (result.status === 'üî¥ CRITICAL DANGER') {
        setCriticalAlerts(getCriticalEvents());
      }
      } catch (err: any) {
      setError(err.message || 'An error occurred during safety analysis');
      console.error('Analysis error:', err);
      setAnalysisProgress(0);
    } finally {
      setAnalyzing(false);
      setTimeout(() => setAnalysisProgress(0), 300);
    }
  };

  // Show landing page if not entered
  if (showLanding) {
    return <LandingPage onEnter={() => setShowLanding(false)} />;
  }

  // Show landing page if not entered
  if (showLanding) {
    return <LandingPage onEnter={() => setShowLanding(false)} />;
  }

  const getStatusColor = (status: string) => {
    if (status.includes('üî¥')) return 'text-red-600 bg-red-50 border-red-200';
    if (status.includes('üü°')) return 'text-yellow-600 bg-yellow-50 border-yellow-200';
    return 'text-green-600 bg-green-50 border-green-200';
  };

  const getZoneColor = (zone: string) => {
    if (zone === 'ZONE_RED') return 'bg-red-100 text-red-700 border border-red-300';
    if (zone === 'ZONE_BLACK') return 'bg-gray-900 text-white border-2 border-red-500';
    if (zone === 'ZONE_YELLOW') return 'bg-yellow-100 text-yellow-800 border border-yellow-300';
    return 'bg-green-100 text-green-700 border border-green-300';
  };

  return (
    <div className="min-h-screen bg-white relative overflow-hidden">
      {/* Pattern Background */}
      <div className="absolute inset-0 opacity-5" style={{
        backgroundImage: `radial-gradient(circle, #9333ea 1px, transparent 1px)`,
        backgroundSize: '30px 30px'
      }}></div>
      
      {/* Header */}
      <header className="relative border-b-2 border-purple-200 bg-white/80 backdrop-blur-md sticky top-0 z-50 shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-20">
            <div className="flex items-center gap-4">
              <div className="text-4xl">üõ°Ô∏è</div>
              <div>
                <h1 className="text-3xl font-bold bg-gradient-to-r from-purple-600 to-purple-400 bg-clip-text text-transparent">
                  SITE GUARDIAN
                </h1>
                <p className="text-sm text-purple-600 font-medium">AUTONOMOUS SAFETY SYSTEM</p>
              </div>
            </div>
            <div className="flex items-center gap-6">
              <label className="flex items-center gap-2 cursor-pointer group">
                <input
                  type="checkbox"
                  checked={audioEnabled}
                  onChange={(e) => setAudioEnabled(e.target.checked)}
                  className="w-5 h-5 text-purple-600 rounded focus:ring-purple-500"
                />
                <span className="text-sm font-medium text-gray-700 group-hover:text-purple-600 transition-colors">
                  üîä Audio Alerts
                </span>
              </label>
              {criticalAlerts.length > 0 && (
                <div className="px-4 py-2 bg-red-100 border-2 border-red-400 rounded-full text-sm font-bold text-red-700 animate-pulse shadow-lg">
                  ‚ö†Ô∏è {criticalAlerts.length} CRITICAL
                </div>
              )}
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Video Upload Section */}
          <div className="lg:col-span-2 space-y-6">
            <div className="bg-white/90 backdrop-blur-sm rounded-2xl border-2 border-purple-100 p-8 shadow-xl">
              <h2 className="text-2xl font-bold text-gray-800 mb-6 flex items-center gap-3">
                <span className="text-3xl">üìπ</span> Video Feed Analysis
              </h2>
              
              <div className="border-2 border-dashed border-purple-300 rounded-xl p-12 text-center hover:border-purple-500 transition-all bg-purple-50/50 mb-6">
                <input
                  type="file"
                  ref={fileInputRef}
                  onChange={handleFileChange}
                  accept="video/*"
                  className="hidden"
                />
                
                {!file ? (
                  <div>
                    <div className="text-7xl mb-6">üé•</div>
                    <p className="text-gray-600 mb-6 text-lg">Upload construction site video for safety analysis</p>
                    <button
                      onClick={() => fileInputRef.current?.click()}
                      className="px-8 py-4 bg-gradient-to-r from-purple-600 to-purple-500 hover:from-purple-700 hover:to-purple-600 text-white rounded-xl font-semibold text-lg shadow-lg hover:shadow-xl transition-all transform hover:scale-105"
                    >
                      Select Video File
                    </button>
                  </div>
                ) : (
                  <div>
                    <div className="text-5xl mb-4">‚úì</div>
                    <p className="text-gray-800 mb-2 font-semibold text-lg">{file.name}</p>
                    <p className="text-sm text-gray-500 mb-6">
                      {(file.size / (1024 * 1024)).toFixed(2)} MB
                    </p>
                    {videoRef && file && (
                      <video
                        ref={videoRef}
                        src={URL.createObjectURL(file)}
                        controls
                        className="w-full max-w-2xl mx-auto rounded-xl shadow-lg mb-6"
                      />
                    )}
                    <div className="flex gap-4 justify-center">
                      <button
                        onClick={() => {
                          setFile(null);
                          setCurrentAnalysis(null);
                          setAnnotatedImage(null);
                          if (fileInputRef.current) fileInputRef.current.value = '';
                        }}
                        className="px-6 py-3 bg-gray-200 hover:bg-gray-300 text-gray-700 rounded-xl font-medium transition-all"
                      >
                        Change File
                      </button>
                      <button
                        onClick={handleAnalyze}
                        disabled={analyzing}
                        className="px-8 py-3 bg-gradient-to-r from-purple-600 to-purple-500 hover:from-purple-700 hover:to-purple-600 disabled:from-gray-400 disabled:to-gray-400 disabled:cursor-not-allowed text-white rounded-xl font-semibold shadow-lg hover:shadow-xl transition-all transform hover:scale-105 disabled:transform-none"
                      >
                        {analyzing ? '‚è≥ ANALYZING...' : 'üîç ANALYZE SAFETY'}
                      </button>
                    </div>
                  </div>
                )}
              </div>

              {error && (
                <div className="mt-6 p-4 bg-red-50 border-2 border-red-200 rounded-xl text-red-700">
                  {error}
                </div>
              )}
            </div>

            {/* Annotated Image Display */}
            {annotatedImage && (
              <div className="bg-white/90 backdrop-blur-sm rounded-2xl border-2 border-purple-100 p-6 shadow-xl">
                <h3 className="text-xl font-bold text-gray-800 mb-4">Annotated Analysis</h3>
                <div className="relative">
                  <img
                    src={annotatedImage}
                    alt="Safety analysis with face annotations"
                    className="w-full rounded-xl shadow-lg"
                  />
                  <canvas ref={canvasRef} className="hidden" />
                </div>
              </div>
            )}

            {/* Current Analysis Results */}
            {currentAnalysis && (
              <div className="bg-white/90 backdrop-blur-sm rounded-2xl border-2 border-purple-100 p-8 shadow-xl">
                <div className="flex items-center justify-between mb-8">
                  <h2 className="text-2xl font-bold text-gray-800">Safety Analysis</h2>
                  <div className={`px-6 py-3 rounded-xl border-2 font-bold text-lg ${getStatusColor(currentAnalysis.status)}`}>
                    {currentAnalysis.status}
                  </div>
                </div>

                {/* Analysis Bullet Points */}
                <div className="mb-8">
                  <h3 className="text-xl font-semibold text-gray-800 mb-4">Analysis:</h3>
                  <ul className="space-y-3 bg-purple-50 rounded-xl p-6 border border-purple-100">
                    {currentAnalysis.analysis.map((point, idx) => (
                      <li key={idx} className="flex items-start gap-3 text-gray-700">
                        <span className="text-purple-600 font-bold mt-1">‚Ä¢</span>
                        <span className="flex-1">{point}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/* Detected Workers */}
                {currentAnalysis.detected && currentAnalysis.detected.length > 0 && (
                  <div className="mb-8">
                    <h3 className="text-xl font-semibold text-gray-800 mb-4">Detected Workers:</h3>
                    <div className="space-y-4">
                      {currentAnalysis.detected.map((worker, idx) => (
                        <div key={idx} className="bg-white border-2 border-purple-100 rounded-xl p-6 shadow-sm hover:shadow-md transition-shadow">
                          <div className="flex items-start justify-between mb-4">
                            <p className="font-semibold text-gray-800 text-lg">{worker.description}</p>
                            <span className={`px-3 py-1 rounded-lg text-sm font-semibold ${getZoneColor(worker.zone)}`}>
                              {worker.zone}
                            </span>
                          </div>
                          <div className="grid grid-cols-3 gap-4">
                            <div className={worker.ppe.hardHat ? 'text-green-600 font-medium' : 'text-red-600 font-semibold'}>
                              {worker.ppe.hardHat ? '‚úì' : '‚úó'} Hard Hat
                            </div>
                            <div className={worker.ppe.safetyVest ? 'text-green-600 font-medium' : 'text-red-600 font-semibold'}>
                              {worker.ppe.safetyVest ? '‚úì' : '‚úó'} Safety Vest
                            </div>
                            <div className={worker.ppe.protectiveBoots ? 'text-green-600 font-medium' : 'text-red-600 font-semibold'}>
                              {worker.ppe.protectiveBoots ? '‚úì' : '‚úó'} Boots
                            </div>
                            <div className={worker.ppe.safetyGlasses ? 'text-green-600 font-medium' : 'text-red-600 font-semibold'}>
                              {worker.ppe.safetyGlasses ? '‚úì' : '‚úó'} Glasses
                            </div>
                            <div className={worker.ppe.harness ? 'text-green-600 font-medium' : 'text-red-600 font-semibold'}>
                              {worker.ppe.harness ? '‚úì' : '‚úó'} Harness
                            </div>
                            <div className={worker.compliance ? 'text-green-600 font-semibold' : 'text-red-600 font-semibold'}>
                              {worker.compliance ? '‚úì Compliant' : '‚úó Violation'}
                            </div>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Action */}
                <div className="mb-8">
                  <h3 className="text-xl font-semibold text-gray-800 mb-4">Action:</h3>
                  <div className="bg-red-50 border-2 border-red-200 p-6 rounded-xl">
                    <p className="font-mono text-red-700 font-semibold text-lg">{currentAnalysis.action}</p>
                  </div>
                </div>

                {/* Voice Output */}
                {currentAnalysis.voiceOutput && (
                  <div>
                    <h3 className="text-xl font-semibold text-gray-800 mb-4">Audio Intervention:</h3>
                    <div className="bg-yellow-50 border-2 border-yellow-200 p-6 rounded-xl">
                      <p className="font-mono text-yellow-800 font-semibold text-lg mb-4">
                        "{currentAnalysis.voiceOutput}"
                      </p>
                      {audioEnabled && (
                        <button
                          onClick={() => speakIntervention(currentAnalysis.voiceOutput!, currentAnalysis.audioLevel)}
                          className="px-6 py-3 bg-yellow-500 hover:bg-yellow-600 text-white rounded-xl font-semibold transition-all shadow-md hover:shadow-lg"
                        >
                          üîä Replay Audio
                        </button>
                      )}
                    </div>
                  </div>
                )}
              </div>
            )}
          </div>

          {/* Sidebar - Alerts & Stats */}
          <div className="lg:col-span-1 space-y-6">
            <div className="bg-white/90 backdrop-blur-sm rounded-2xl border-2 border-purple-100 p-6 shadow-xl">
              <h2 className="text-xl font-bold text-gray-800 mb-6">System Status</h2>
              <div className="space-y-4">
                <div className="flex items-center justify-between p-4 bg-green-50 rounded-xl border border-green-200">
                  <span className="text-gray-700 font-medium">Video Feed</span>
                  <span className="text-green-600 font-bold">‚úì ONLINE</span>
                </div>
                <div className="flex items-center justify-between p-4 bg-green-50 rounded-xl border border-green-200">
                  <span className="text-gray-700 font-medium">Audio System</span>
                  <span className={audioEnabled ? 'text-green-600 font-bold' : 'text-gray-400'}>
                    {audioEnabled ? '‚úì ENABLED' : '‚úó DISABLED'}
                  </span>
                </div>
                <div className="flex items-center justify-between p-4 bg-green-50 rounded-xl border border-green-200">
                  <span className="text-gray-700 font-medium">AI Analysis</span>
                  <span className="text-green-600 font-bold">‚úì ACTIVE</span>
                </div>
              </div>
            </div>

            {/* Critical Alerts */}
            {criticalAlerts.length > 0 && (
              <div className="bg-red-50 border-2 border-red-300 rounded-2xl p-6 shadow-xl">
                <h2 className="text-xl font-bold text-red-700 mb-4">üî¥ Critical Alerts</h2>
                <div className="space-y-3 max-h-96 overflow-y-auto">
                  {criticalAlerts.slice(0, 5).map((alert) => (
                    <div key={alert.id} className="bg-white p-4 rounded-xl border-2 border-red-200 shadow-sm">
                      <p className="text-sm text-red-700 font-bold mb-2">{alert.status}</p>
                      <p className="text-xs text-gray-600 line-clamp-2">{alert.analysis}</p>
                      <p className="text-xs text-gray-400 mt-2">
                        {new Date(alert.timestamp).toLocaleTimeString()}
                      </p>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      </main>
    </div>
  );
}
