// Safety event storage and logging system

export interface SafetyEvent {
  id: string;
  timestamp: string;
  status: 'üî¥ CRITICAL DANGER' | 'üü° WARNING' | 'üü¢ CLEAR';
  workers: any[];
  analysis: string;
  action: string;
  voiceOutput: string;
  audioLevel?: string;
  resolved: boolean;
  videoUrl?: string;
}

const STORAGE_KEY = 'site_guardian_events';

export function saveSafetyEvent(event: SafetyEvent): void {
  if (typeof window === 'undefined') return;
  
  const existing = getAllSafetyEvents();
  existing.unshift(event); // Add to beginning (most recent first)
  // Keep only last 100 events
  if (existing.length > 100) {
    existing.splice(100);
  }
  localStorage.setItem(STORAGE_KEY, JSON.stringify(existing));
}

export function getAllSafetyEvents(): SafetyEvent[] {
  if (typeof window === 'undefined') return [];
  
  const stored = localStorage.getItem(STORAGE_KEY);
  return stored ? JSON.parse(stored) : [];
}

export function getSafetyEventById(id: string): SafetyEvent | null {
  const events = getAllSafetyEvents();
  return events.find(e => e.id === id) || null;
}

export function markEventResolved(id: string): void {
  if (typeof window === 'undefined') return;
  
  const events = getAllSafetyEvents();
  const event = events.find(e => e.id === id);
  if (event) {
    event.resolved = true;
    localStorage.setItem(STORAGE_KEY, JSON.stringify(events));
  }
}

export function deleteSafetyEvent(id: string): void {
  if (typeof window === 'undefined') return;
  
  const events = getAllSafetyEvents().filter(e => e.id !== id);
  localStorage.setItem(STORAGE_KEY, JSON.stringify(events));
}

export function generateEventId(): string {
  return `EVENT_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
}

export function getCriticalEvents(): SafetyEvent[] {
  return getAllSafetyEvents().filter(e => e.status === 'üî¥ CRITICAL DANGER' && !e.resolved);
}

